}
.badge.blue {
  background-color: #6ecff5;
}
.badge.purple {
  background-color: #8e44ad;
}
/*---------------------
    button
---------------------*/
.button {
  background-image: none !important;
  box-shadow: none !important;
  font-weight: 200;
}
.olist-search .button {
  border-radius: 0;
}
.spaced .button {
  margin-right: 8px;
  margin-bottom: 8px;
}
.iselect {
  font-size: 1em;
  border: 1px solid #eee;
  outline: none;
  background-color: #fff;
  color: #838fa1;
  border-radius: 0.1428571em;
  font-family: 'Microsoft YaHei', Tahoma, Helvetica, Arial, "\5b8b\4f53", sans-serif;
  transition: background-color 0.3s ease-out, box-shadow 0.2s ease, border-color 0.2s ease;
  box-shadow: 0em 0em 0em 0em rgba(0, 0, 0, 0.3) inset;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  box-sizing: border-box;
  height: 2.57142857em;
  line-height: 2.42857142em;
  display: inline-block;
  position: relative;
  min-width: 31.6239%;
  vertical-align: middle;
}
.iselect:hover,
.iselect:hover ul,
.iselect.open,
.iselect.open ul {
  border-color: #9ca8b0;
}
.iselect-value {
  position: absolute;
  padding-left: 0.5em;
  left: 0;
  height: 2.42857142em;
  right: 1.5em;
  overflow: hidden;
  cursor: default;
}
.iselect-handle {
  position: absolute;
  height: 2.42857142em;
  width: 1.5em;
  right: 0;
  overflow: hidden;
  cursor: default;
}
.iselect-handle::before {
  content: "";
  position: absolute;
  top: 0.75em;
  right: 0.35714268em;
  width: 0px;
  height: 0;
  border: 0.25em solid #fff;
  border-bottom: 0.42857143em solid #838fa1;
  border-top: 0;
  opacity: 0.8;
}
.iselect-handle::after {
  content: "";
  position: absolute;
  top: 1.35714268em;
  right: 0.35714268em;
  width: 0px;
  height: 0;
  border: 0.25em solid #fff;
  border-top: 0.42857143em solid #838fa1;
  border-bottom: 0;
  opacity: 0.8;
}
.iselect-search {
  display: none;
  position: absolute;
  width: 100%;
  top: 2.2857143em;
  left: -1px;
  z-index: 4;
  padding: 1em;
  box-sizing: border-box;
}
.iselect-search label {
  display: block;
  width: auto;
  font-size: 12px;
}
.iselect ul {
  display: none;
  position: absolute;
  width: 100%;
  top: 2.2857143em;
  left: -1px;
  background-color: #fff;
  transition: background-color 0.3s ease-out, box-shadow 0.2s ease, border-color 0.2s ease;
  border: 1px solid #eee;
  border-top: 0;
  padding-bottom: 0.5em;
  padding-top: 4.2857143em;
  border-bottom-left-radius: 0.1428571em;
  border-bottom-right-radius: 0.1428571em;
  z-index: 3;
}
.iselect-nosearch.iselect ul {
  padding-top: 10px;
}
.iselect ul li {
  box-sizing: border-box;
  padding: 0 1em;
  cursor: default;
}
.iselect ul li:hover {
  background-color: rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease-out;
}
.iselect.open ul {
  display: block;
}
.iselect.open .iselect-search {
  display: block;
}
.iselect-little {
  font-size: 12px;
  height: 2em;
  line-height: 1.83333333em;
}
.iselect-little.iselect ul {
  top: 1.83333333em;
}
.iselect-little.iselect ul li {
  padding: 3px 1em;
}
.iselect-little .iselect-value {
  height: 2em;
}
.iselect-little .iselect-handle {
  height: 1.71428571em;
  font-size: 14px;
}
.iselect-little .iselect-handle::before {
  top: 0.35714268em;
}
.iselect-little .iselect-handle::after {
  top: 0.92857143em;
}
/*---------------------
    additems
---------------------*/
.additem-head {
  width: 100%;
  height: 36px;
  position: relative;
}
.additem-head input,
.additem-search-list {
  width: 142px;
}
.additem-search-list {
  position: absolute;
  top: 36px;
  left: 0;
  box-sizing: border-box;
  padding: 0.5em;
  border: 1px solid #838fa1;
  background-color: #fff;
}
.additem-search-list li {
  box-sizing: border-box;
  padding: 0 1em;
  cursor: default;
}
.additem-search-list li:hover {
  background-color: rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease-out;
}
.additem-head ul.hide {
  display: none;
}
.additems {
  margin-top: 20px;
}
.additems li {
  display: inline-block;
  margin: 10px 10px 10px 0px;
  line-height: 18px;
}
.additems li span.remove {
  display: inline-block;
  background-color: #fcdddf;
  border: 1px solid #fcdddf;
  cursor: pointer;
  width: 16px;
  height: 16px;
  line-height: 14px;
  text-align: center;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 3px;
}
.additems li span.remove:hover {
  border: 1px solid #fa5a64;
  color: #fa5a64;
}
/*---------------------
    upload files
---------------------*/
.upload {
  width: 100%;
}
.upload-file {
  margin: 10px 0;
  width: 100%;
  box-sizing: border-box;
  border: 2px dashed #d1dae6;
  border-radius: 10px;
  line-height: 28px;
  color: #d1dae6;
  text-align: center;
  font-size: 14px;
}
.upload-file i {
  font-size: 28px;
}
.upload-file:hover {
  border: 2px dashed #9ca8b0;
  color: #9ca8b0;
  cursor: pointer;
}
.upload-input {
  padding: 100px 0;
  height: 80px;
}
.upload-item {
  text-align: center;
  margin-bottom: 6px;
  position: relative;
  padding: 50px;
  height: 180px;
}
.upload-item li {
  width: 160px;
  height: 120px;
  margin: 0 10px;
  display: inline-block;
  vertical-align: middle;
}
.upload-item li .img-box {
  width: 160px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.upload-item li img {
  max-width: 120px;
  max-height: 120px;
  vertical-align: middle;
  -webkit-transition: all 0.4s ease-in-out;
}
.upload-item .loading {
  border-radius: 6px;
  padding: 4px;
  background-color: #f4f6f9;
  margin-top: 6px;
}
.upload-item .bar {
  border-radius: 4px;
  background-color: #00a2ca;
  height: 4px;
}
.upload-item .text {
  margin-top: 6px;
  font-size: 12px;
}
/* add media */
.add-media li {
  width: 240px;
  height: 240px;
  background-color: #f4f6f9;
  border: 1px solid #f4f6f9;
  text-align: center;
  line-height: 240px;
  overflow: hidden;
  margin: 10px auto;
}
.add-media li img {
  max-width: 240px;
  max-height: 240px;
  vertical-align: middle;
  -webkit-transition: all 0.4s ease-in-out;
}
/* tabs box */
.tabs {
  margin-top: 10px;
}
.tabs-header {
  width: 100%;
}
.tabs-header li {
  float: left;
  font-size: 12px;
  border: 1px solid #eee;
  background-color: #f7f8fa;
  text-align: center;
  margin-left: -1px;
  line-height: 36px;
  box-sizing: border-box;
  cursor: pointer;
  padding: 0 18px;
}
.tabs-header li.active {
  border-top: 2px solid #00a2ca;
  background-color: #fff;
  line-height: 35px;
}
/*==================================
              Header
 =================================*/
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #2b3643;
  z-index: 20;
}
/* header-> left */
.logo-box {
  width: 235px;
  height: 50px;
  padding: 0 20px;
  box-sizing: border-box;
  float: left;
}
.logo-box a {
  float: left;
  margin-top: 15px;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bolder;
  color: #fff;
  line-height: 20px;
  font-family: optima;
}
.logo-box a span {
  color: #f36a5a;
}
/* header-> right */
.head-tools-box {
  float: right;
}
.head-tools {
  float: right;
  padding-right: 10px;
}
.head-tools > li {
  float: left;
  position: relative;
}
.head-tools > li ul {
  display: none;
  min-width: 220px;
}
.head-tools > li:hover ul {
  display: block;
}
.head-tools a.toggler {
  display: block;
  line-height: 50px;
  padding: 0 16px;
  font-size: 17px;
  color: #79869a;
  position: relative;
}
.head-tools a.toggler:hover {
  background-color: #3f4f62;
}
.head-tools span.avatar {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  margin-right: 6px;
  overflow: hidden;
}
.avatar img {
  width: 30px;
}
.head-tools a.toggler .badge {
  position: absolute;
  right: 25px;
  top: 10px;
  font-size: 12px;
}
/*==================================
              Sidebar
 =================================*/
.sidebar-box {
  position: absolute;
  left: 0;
  top: 50px;
  z-index: 10;
  width: 235px;
  background-color: #364150;
}
.closed .sidebar-box {
  width: 46px;
}
.sidebar-toggle {
  padding: 8px 0;
  font-size: 12px;
  color: #fff;
  opacity: 0.6;
  text-align: center;
  border-bottom: 1px solid #3d4957;
}
.sidebar-toggle:hover {
  opacity: 0.9;
}
.sidebar-toggle i {
  display: inline-block;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.closed .sidebar-toggle i {
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
/* navigate */
.navigate li.nav-item {
  border-bottom: 1px solid #3d4957;
  position: relative;
}
.navigate a.nav-link {
  display: block;
  padding: 10px 15px;
  line-height: 20px;
  color: #b4bcc8;
  font-size: 14px;
  font-weight: 300;
  position: relative;
}
.navigate a.nav-link:hover {
  background-color: #3e4b5c;
}
.navigate a.nav-link i {
  margin-left: 2px;
  margin-right: 10px;
  font-size: 16px;
  color: #6b788b;
}
.closed .navigate a {
  padding: 10px 0;
  text-align: center;
}
.closed .navigate a.nav-link i {
  margin: 0;
}
.closed .navigate a span {
  display: none;
}
.closed .navigate li ul {
  display: none;
  position: absolute;
  left: 46px;
  top: -20px;
  background-color: #2b3643;
  width: 180px;
  padding: 10px 0;
}
.closed .navigate li:hover ul {
  display: block;
}
.closed .navigate li ul::before {
  width: 8px;
  height: 8px;
  display: block;
  content: '';
  position: absolute;
  left: -4px;
  top: 30px;
  background-color: #2b3643;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.closed .navigate li {
  width: 100%;
  height: 40px;
}
.closed .navigate li ul a.nav-link {
  padding: 6px;
}
/* navigate -> arrow */
.nav-link .arrow {
  display: inline-block;
  position: absolute;
  top: 15px;
  right: 20px;
  width: 7px;
  height: 10px;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.nav-link:hover .arrow,
li.open .nav-link .arrow {
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.nav-link .arrow::before,
.nav-link .arrow::after {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  width: 0px;
  height: 0px;
  border-right: 5px solid #586b7c;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
}
.nav-link .arrow::after {
  left: 2px;
  border-right: 5px solid #364150;
}
.nav-link:hover .arrow::after {
  border-right: 5px solid #3e4b5c;
}
/* navigate -> sub-nav */
.navigate .sub-nav {
  margin: 8px 0;
}
.navigate .sub-nav li.nav-item {
  border-bottom: 0;
}
.navigate .sub-nav a.nav-link {
  padding: 6px 15px 6px 43px;
}
/*==================================
              Main
 =================================*/
.main-box {
  margin-left: 235px;
  margin-top: 30px;
  padding: 25px 20px 10px;
  background-color: #fff;
  min-height: 2000px;
}
.closed .main-box {
  margin-left: 46px;
}
/* page-bar */
.page-bar {
  padding: 10px 0;
}
.page-bar-left {
  float: left;
}
.page-bar-right {
  float: right;
}
/* section */
.section {
  padding: 12px 20px 15px;
  border: 1px solid #e7ecf1;
  border-radius: 1px;
}
.section-head {
  padding: 0 0 10px 0;
  margin-bottom: 10px;
  border-bottom: 1px solid #eef1f5;
}
.section-head::after {
  clear: both;
  display: table;
  line-height: 0;
  content: "";
}
.section-head-left {
  float: left;
  margin: 0;
  font-size: 16px;
  font-weight: 300;
  line-height: 36px;
}
.section-head-right {
  float: right;
}
.section-content {
  height: 220px;
}
/*==================================
            Object List
 =================================*/
.olist-header-l {
  float: left;
}
.olist-header-r {
  float: right;
}
/* crumbs */
.crumbs-box {
  line-height: 18px;
}
.crumbs-first {
  float: left;
  margin-top: 1px;
}
.crumbs-box b {
  font-weight: 400;
  font-size: 16px;
}
.crumbs-arrow {
  float: left;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  line-height: 14px;
  margin: 3px 6px 0 6px;
  text-align: center;
  color: #fff;
}
.crumbs-dot::before {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #999;
  position: absolute;
  left: 7px;
  top: 8px;
}
.crumbs {
  float: left;
  position: relative;
  font-size: 12px;
  cursor: default;
  margin-right: 6px;
  background-color: #fcdddf;
  box-sizing: border-box;
  border: 1px solid #fcdddf;
  border-radius: 2px;
  padding: 0 18px 0 6px;
}
.crumbs:hover {
  border: 1px solid #fa5a64;
}
.crumbs::after {
  content: "×";
  right: 5px;
  cursor: pointer;
  top: 3px;
  position: absolute;
  line-height: 10px;
}
.crumbs:hover::after {
  color: #fa5a64;
}
.crumbs-main {
  width: 452px;
  display: none;
  position: absolute;
  top: 28px;
  left: -1px;
  border: 1px solid rgba(39, 41, 43, 0.15);
  border-radius: 4px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
  padding: 10px;
  background-color: #fff8db;
  color: #b58105;
  z-index: 1;
}
.crumbs-main::before {
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  top: -4px;
  left: 24px;
  overflow: hidden;
  display: block;
  background-image: none;
  background-color: #000000;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  z-index: 2;
  border-style: solid;
  border-width: 1px;
  border-color: #000000;
  background-color: #fff8db;
  border-color: rgba(39, 41, 43, 0.15);
  border-bottom: 0;
  border-right: 0;
}
.crumbs-main .title {
  padding: 0em 0em 0.5em;
  font-size: 14px;
}
.crumbs:hover .crumbs-main {
  display: block;
}
.crumbs-main span {
  display: inline-block;
  min-width: 100px;
  margin-right: 12px;
  line-height: 2em;
}
/* olist -> tools */
.tools {
  display: block;
  float: left;
  height: 36px;
  min-width: 36px;
  position: relative;
}
a.tools,
.tools > .dropdown-toggler {
  display: block;
  box-sizing: border-box;
  min-width: 36px;
  text-align: center;
  height: 36px;
  line-height: 36px;
  cursor: pointer;
  vertical-align: middle;
  color: #fff;
  padding: 0 6px;
}
.tools h4 {
  font-size: 16px;
  margin-top: 0;
}
.olist-header-l .tools {
  margin-right: 10px;
  z-index: 2;
  border-radius: 0;
}
.olist-header-r .tools {
  margin-left: 10px;
  z-index: 2;
  border-radius: 0;
}
.dropdown-toggler .icon-arrow-down {
  font-size: 12px;
}
/* olist -> search */
.olist-search {
  float: left;
}
.olist-search input {
  border: 0;
  background: #f3f6f9;
  height: 36px;
  float: left;
  width: 280px;
  padding-right: 0;
}
.olist-operation,
.olist-type {
  width: 68px;
}
.search-clear {
  height: 36px;
  width: 1px;
  position: relative;
  display: inline-block;
  background: #f3f6f9;
  vertical-align: middle;
}
.search-clear span {
  position: absolute;
  top: 12px;
  right: 4px;
  line-height: 12px;
  text-align: center;
  display: inline-block;
