  width: 100%;
}
.olist-table tbody tr:hover {
  background-color: #eef1f5;
}
.olist-table th {
  white-space: nowrap;
  background: none;
  position: relative;
  box-sizing: border-box;
  height: 36px;
  padding: 0;
  min-width: 48px;
  border-left: 1px solid #e7ecf1;
  border-top: 1px solid #e7ecf1;
  padding-left: 6px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
.olist-table td {
  white-space: nowrap;
  padding: 0;
  vertical-align: top;
  box-sizing: border-box;
  border-left: 1px solid #e7ecf1;
  border-bottom: 1px solid #e7ecf1;
}
.olist-table thead > tr {
  background-color: #f4f6f9;
}
.olist-table tr > th:last-child,
.olist-table tr > td:last-child {
  border-left: 0;
}
.olist-table th strong,
.drag-mirror strong {
  display: block;
  width: calc(100% - 18px);
  height: 36px;
  overflow: hidden;
  text-indent: 6px;
  line-height: 36px;
  font-size: 12px;
  font-weight: normal;
  color: #999;
  cursor: move;
}
.olist-table .td-cell {
  padding: 12px;
  line-height: 26px;
  white-space: nowrap;
  overflow: hidden;
  width: calc(100% - 24px);
}
.ebtn {
  width: 1.7142em;
  height: 1.7142em;
  display: inline-block;
  vertical-align: middle;
  line-height: 1.8571em;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  color: #fff;
}
.ebtn:hover,
a.ebtn:hover {
  opacity: 0.8;
