}
.olist-table.mirror {
  position: absolute;
  border-left: 1px solid #e7ecf1;
  transition: all 0.3s ease-in-out;
}
.olist-table.current-mirror {
  opacity: 0.6;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
  z-index: 2;
  transition: none;
}
table td.active,
table td.hover {
  background-image: linear-gradient(to top, #ced6df, #b6c6d7);
}
table.moving td,
table.moving th {
  opacity: 0;
  border-left: 0;
}
table.moving td.row-checkbox,
table.moving th.row-checkbox,
table.moving td.row-action,
table.moving th.row-action,
table.moving th.row-last {
