/* olist -> order-by resize */
.olist-table .resize {
  display: block;
  height: 36px;
  width: 7px;
  position: absolute;
  right: -4px;
  left: auto;
  top: 0;
  cursor: col-resize;
  z-index: 8;
}
.olist-table th .order {
  position: absolute;
  right: 4px;
  top: 3px;
  width: 14px;
  height: 30px;
  cursor: pointer;
}
.olist-table th .order::before {
  content: "";
  width: 4px;
  height: 4px;
  position: absolute;
  top: 12px;
  left: 5px;
  border-right: 1px solid #666;
  border-bottom: 1px solid #666;
  display: block;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  display: none;
}
.olist-table th:hover .order::before {
  display: inline-block;
}
.olist-table th .order.asc::before {
  display: inline-block;
}
.olist-table th .order.desc::before {
  display: inline-block;
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
  top: 14px;
}
