  transition: all 0.3s ease-in-out;
}
.ilink,
.ilinks a {
  position: relative;
}
.ilink::before,
.ilinks a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: #50d2fa;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
  top: 1.2em;
}
.ilink:hover::before,
.ilinks a:hover::before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  transition: all 0.3s ease-in-out 0s;
}
